.principal{
    background-color: #00A5BE;
    min-height: 100vh;
}
.header2{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    height: 24vh;
}
.imagem404{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5vh;

}
.imagem404 h3{
    color: white;
    font-size: 3vw;
    margin: 0;
    margin-top: -2vw;
}
.img404{
    background-image: url("/public/API Rick & morty (1).png");
    background-size: contain;
    background-repeat: no-repeat;
    height: 27vw;
    width: 48vw;
}

.bola{
    height: 20vw;
    width: 20vw;
    background: #F7391C;
    border-radius: 50%;
    margin-top: -10vw;
}

.detalhe{
    background-image: url("/public/detalhe.png");
    background-size: contain;
    background-repeat: no-repeat;
    height: 50vh;
    width: 2vh;
    position: fixed;
    top: 33vh;
    right: 3vw;
}
.detalhe2{
    background-image: url("/public/detalhe.png");
    background-size: contain;
    background-repeat: no-repeat;
    height: 50vh;
    width: 2vh;
    position: fixed;
    top: 33vh;
    left: 3vw;
}

.input{
    position: fixed;
    top: 4vw;
    right: 4vw;
    padding: 2vw;
    width: 25vw;
    height: 3vw;
    border-radius: 6vw;
    border: none;
    outline: none;
    font-size: 2vw;
}
.button {
    position: fixed;
    top: 4.1vw;
    right: 25vw;
    padding: 1vw;
    width: 7vw;
    height: 6vw;
    border-radius: 6vw;
    border: none;
    background: white;
    outline: white;
    transition: 1s;
}

.lupa:hover button{
    right: 6vw;
    transition: 1s;
}

.lupa2{
    background-image: url("/public/lupa.png");
    width: 6vw;
    height: 6vw;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
}

.img{
    background-image: url("/public/logo2.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 22vw;
    height: 10vw;
    margin-top: 4vw;
}

.lupa{
    margin-top: 2vw;
    height: 1vw;
}
.tudoper{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 76vh;

}

.personagem{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    width: 76vw;
    align-items: center;
    justify-items: center;
    margin-top: -1vh;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #00A5BE;
    border-radius: 0.5vw;

}

.infos{
    background: white;
    width: 20vw;
    padding: 1vw;
    text-align: left;
    border-radius: 1vw;
    margin-top: 1vw;
    font-size: 2vw;
    font-weight: 600;
}
.name{
    font-size: 3vw;
    width: 26vw;
    margin-top: 0;
}
.foto{
    border-radius: 2vw;
    background: linear-gradient(145deg, #00b1cb, #0095ab);
    box-shadow:  20px 20px 60px #008ca2,
    -20px -20px 60px #00bedb;
    width: 27vw;
    height: 27vw;
}