.header{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    background-image: url("/public/fundo.png");
    background-size: cover;
    height: 100vh;
    justify-items: center;
    background-position: center;
}

.principal2{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    background-image: url("/public/fundo.png");
    background-size: cover;
    height: 100vh;
    justify-items: center;
    background-position: center;
}

.diviframe iframe{
    width: 81vw;
    height: 88vh;
}

.diviframe{
    position: absolute;
    top: 3vw;
    left: 7vw;

}

.tvbotao{
    position: fixed;
    right: 2vw;
    top: 3.6vw;
    background: white;
    padding: 2vw;
    border: none;
    border-radius: 50%;
    cursor: pointer;
}

.tvimg{
    background-image: url("/public/download.png");
    width: 3.6vw;
    height: 3.6vw;
    background-repeat: no-repeat;
    background-size: contain;
}

.tvimg2{
    background-image: url("/public/download.png");
    width: 3.6vw;
    height: 3.6vw;
    background-repeat: no-repeat;
    background-size: contain;
}



.input{
    position: absolute;
    top: 4vw;
    right: 12vw;
    padding: 2vw;
    width: 25vw;
    height: 3vw;
    border-radius: 6vw;
    border: none;
    outline: none;
    font-size: 1.6vw;
}
.button {
    position: relative;
    top: 2.1vw;
    right: 20vw;
    padding: 1vw;
    width: 7vw;
    height: 6vw;
    border-radius: 6vw;
    border: none;
    background: white;
    outline: white;
    transition: 1s;
}

.lupa:hover button{
    right: 0vw;
    transition: 1s;
}

.lupa2{
    background-image: url("/public/lupa.png");
    width: 6vw;
    height: 6vw;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
}

.img{
    background-image: url("/public/logo.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 22vw;
    height: 10vw;
    margin-top: 2vw;
}

.lupa{
    margin-top: 2vw;
}
